$navbarColor: rgba(33, 38, 42, 0.8);
$primaryColor: #202020;
$primaryColorFaded: #676a73;
$secondaryColor: #1a1a1a;
$menuActiveBackgroundColor: rgba(0, 0, 0, 0.4);
$mobileMenuActiveBackgroundColor: rgba(0, 0, 0, 0.88);
$accentColor: #23c5d9;
$accentColorFaded: rgba(35, 194, 215, 0.6);
$textColor: rgb(247, 247, 247);
$textHeaderColor: rgb(247, 247, 247);
$editorPrimary: #1f2329;
$editorSecondary: #2c3138;
$editorBorder: rgb(146, 146, 146);
$navbarHeight: 70px;
$navbarHeightMobile: 60px;
$techContainerBackgroundColor: rgba(123, 128, 145, 0.3);
$scrollThumbColor: rgba(115, 115, 115, 0.7);
$scollTrackColor: transparent;
$skillsContainerBackgroundColor: #24272c;
$testColor: #9b4cfb;

$containerBorderRadius: 10px;

$width: 768px;

* {
  -webkit-tap-highlight-color: transparent;
  letter-spacing: 0.2px;
  max-height: 100vh;
}

@mixin mobile {
  @media (min-height: 421px) {
    @media (max-width: #{$width}) {
      @content;
    }
  }
}

@mixin smallDesktop {
  @media (min-height: 1px) {
    @media (max-width: 1300px) and (min-width: 820px) {
      @content;
    }
  }
}

@mixin mobileKeyboard {
  @media (max-height: 500px) {
    @media (max-width: 500px) {
      @content;
    }
  }
}

@mixin mobileHorizontal {
  @media (max-height: 420px) {
    @media (min-width: 700px) {
      @content;
    }
  }
}

// scrollbar
::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}

::-webkit-scrollbar-thumb {
  background: $primaryColorFaded;
  border-radius: 10px;
}

::-webkit-scrollbar-track {
  background: $secondaryColor;
}

::-webkit-resizer {
  border-right: 2px solid $primaryColorFaded;
  border-bottom: 2px solid $primaryColorFaded;
  cursor: pointer;
  background: linear-gradient(
    135deg,
    transparent 0%,
    transparent 50%,
    $primaryColorFaded 51%,
    $primaryColorFaded 100%
  );
}

// tech colors
// react
$reactColor: #61b8fb;
.react-color-bg {
  background-color: $reactColor;
}
.react-color-bg-hover {
  background-color: $techContainerBackgroundColor;

  &:hover {
    background-color: $reactColor;
    color: black;
  }
}
.react-color-text {
  color: $reactColor;
}

